html,
body {
  margin: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1 {
  display: none;
}

.calendar {
  display: flex;
  width: ((100px + 1) * 7);
  flex-wrap: wrap;
  counter-reset: dayCount;

  & div {
    background-color: #eee;
    width: 100px;
    height: 100px;
    margin: 0 1px 1px 0;
    border: solid 1px black;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 40px;
    line-height: 100px;
    text-align: center;
    transition: all 0.5s;
    box-sizing: border-box;

    &:after {
      counter-increment: dayCount;
      content: counter(dayCount);
    }

    &:hover {
      background-color: pink;
      margin-top: -20px;
    }
  }
}

@for $i from 28 through 31 {
  .calendar-#{$i} div:nth-child(n + #{$i+1}) {
    display: none;
  }
}

$weekdays: (mon: 0, tue: 1, wed: 2, thu: 3, fri: 4, sat: 5, sun: 6);

@each $day, $num in $weekdays {
  .calendar-#{$day}::before {
    content: "";
    width: ($num * 100px) + $num;
  }
}
